import React, {useState, useRef} from 'react';

 const Update = () => {


let  [putVal, setPutData] = useState({ 
uid : " ",
pid : " ",
title : " ",
content : " "

});

const form = useRef(null)

function handle(e) {
const newupdate= { ...putVal};
newupdate[e.target.id]=e.target.value;
setPutData(newupdate);

}

function submit (e) {
e.preventDefault();
 const formputData = new FormData(form.current)
   fetch('https://jsonplaceholder.typicode.com/posts/1', { method: 'PUT', body: formputData })
        .then(response => response.json())
        .then(data => {
alert("Data updated successfully");
}
)
.catch (e => {
alert(e);
})
}

return (
<center>
 <h4>Please enter any value that is already exist and you want to update that value</h4>
<form ref={form} onSubmit={(e) => submit(e)}>
 <input type="text" name="uid" onChange={(e) => handle(e)}  placeholder=" Enter UserId" />
  <input type="text" name="pid" onChange={(e) => handle(e)}  placeholder=" Enter Id" />
 
  <input type="text" name="title"  onChange={(e) => handle(e)}  placeholder=" Enter Title" />

  <input type="text" name="content" onChange={(e) => handle(e)} placeholder="Enter data" />
  <input type="submit" value="Submit" />
</form>

</center>
);}
 
export default Update;