import './App.css';
import Extract from './extract.js';
import Post from './post.js';
import Update from './update.js';

import history from './history';

function Hone() {

return (

<center>
 <form>

         <input type="button"  onClick={() => history.push('/Extract')} value="Fetch Data"/><h3>Click button to fetch data</h3>
<input type="button" onClick={() => history.push('/Post')} value="Create Data"/><h3>Click button to create data</h3>
<input type="button" onClick={() => history.push('/Update')}  value="Update Data"/><h3>Click button to update data</h3>
          </form>
</center>


);
}

export default Hone;
