import React, {useState, useRef} from 'react';

 const Post = () => {


let  [postVal, setPostData] = useState({ 
uid : " ",
pid : " ",
title : " ",
content : " "

});

let [error, setError] = useState(null);

const form = useRef(null)

function handle(e) {
const newdata= { ...postVal};
newdata[e.target.id]=e.target.value;
setPostData(newdata);

}

function submit (e) {
e.preventDefault();
 const formDatas = new FormData(form.current)
   fetch('https://jsonplaceholder.typicode.com/posts/', { method: 'POST', body: formDatas })
        .then(response => response.json())
        .then(data => {
 alert("Data successfully added");
}
)
.catch (e => {
alert(e);
})

}

return (

<center>
<form ref={form} onSubmit={(e) => submit(e)}>
 

  <input type="text" name="uid" onChange={(e) => handle(e)}  placeholder=" Enter UserId" />
  <input type="text" name="pid" onChange={(e) => handle(e)}  placeholder=" Enter Id" />
 
  <input type="text" name="title"  onChange={(e) => handle(e)}  placeholder=" Enter Title" />

  <input type="text" name="content" onChange={(e) => handle(e)} placeholder="Enter data" />
  <input type="submit" value="Submit" />
</form>
</center>
);}
 
export default Post;
