
import React,{useState , useEffect} from 'react';


const Extract = () => {

let  [responseData, setResponseData] = useState([ ]);


useEffect(( ) => {
   
    fetch('https://jsonplaceholder.typicode.com/posts')
        .then(response => response.json())
        .then(data => {
 setResponseData(data);
console.log(data);

})
.catch (e => {

console.log(e);
})

});

return (
    <div className="App">
    <center> Fetching Data from API </center>
   <table>
<thead className="heading">
<tr>
<th>Id</th>
<th>Title</th>
<th>Body</th>
</tr>
</thead>
<tbody>
{responseData.map((p) => {

return <Display key={p.id} data={p} />


})}
</tbody>
</table>
   

      
    </div>
  );
}

const Display = ({data}) => {
return (


<tr>
<td> {data.id}</td>
<td> {data.title}</td>
<td> {data.body}</td>
</tr>


);

}

export default Extract;

